import UserAvatar from '~/components/user/UserAvatar'

export default {
  name: 'ChatAvatar',
  components: {
    UserAvatar
  },
  props: {
    item: {
      type: Object,
      required: true
    }
  },
  computed: {
    isMine() {
      return this.$store.state.user.slug === this.item.slug
    }
  }
}
